/* SegmentPage.css */

.segment-container {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

h1.header {
  text-align: left;
  padding: 20px;
  font-size: 20px;
  font-weight: bold;
  background-color: #2e8a95;
  border: 1px solid #ccc;

  margin-bottom: 20px;
  margin-top: 20px;
  color: white;
}
h3.segment{
  text-align: left;
  background-color: #2e8a95;
  font-size: 20px;
  padding: 20px;
  color: white;

}
button.save {
  margin-right: 75%;
  margin-top: 30px;
  background-color: #7a7a7a;
  color: #fff;
  padding: 10px 30px;
  border: none;
  cursor: pointer;
  margin-bottom: 20px;
  font-size: large;
}
button.save:hover {
  background-color: rgb(209, 202, 202);
  color: black;
}
button {
  background-color: #7a7a7a;
  color: #fff;
  padding: 10px 30px;
  border: none;
  cursor: pointer;
  margin-bottom: 20px;
  font-size: large;
  margin-left: 15px;
}

button:hover {
  background-color: #0056b3;
}

.popup {
  background-color: #f9f9f9;
  /* border: 1px solid #ccc; */
  padding: 20px;
  border-radius: 5px;
  max-width: 400px;
  margin: 0 auto;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input[type="text"],
select {
  width: 95%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button#addSchemaBtn {
  background-color: #28a745;
  color: #fff;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  margin-left: 10px;
}

button#addSchemaBtn:hover {
  background-color: #1e7e34;
}

h3 {
  margin-top: 20px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 5px;
}
button.saveseg{
  background-color: rgb(5, 104, 5);
}

button.cancel{
  background-color: white;
  color:red;
}



